import React, { useState } from 'react';
import { Box, Button, Input, Radio, RadioGroup, Stack, Text, Textarea, useToast } from '@chakra-ui/react';
import useColorModeStyles from '@/utils/useColorModeStyles';

const MainRegister = () => {
  const [step, setStep] = useState(1);
  const [formData, setFormData] = useState({});
  const { bg, textColor, instagramGradient, hoverColor } = useColorModeStyles();
  const toast = useToast();

  const steps = [
    {
      title: "What's your email?",
      description: "To create or login to your account",
      component: (
        <Input
          placeholder="Enter your email"
          type="email"
          onChange={(e) => updateFormData('email', e.target.value)}
        />
      ),
    },
    {
      title: "Enter the confirmation code",
      description: "Check your email for the code we sent you.",
      component: (
        <Input
          placeholder="Enter confirmation code"
          type="text"
          onChange={(e) => updateFormData('confirmationCode', e.target.value)}
        />
      ),
    },
    {
      title: "What's your first name?",
      description: "Let us know how to address you.",
      component: (
        <Input
          placeholder="Enter your first name"
          type="text"
          onChange={(e) => updateFormData('firstName', e.target.value)}
        />
      ),
    },
    {
      title: "What's your last name?",
      description: "Share your family name.",
      component: (
        <Input
          placeholder="Enter your last name"
          type="text"
          onChange={(e) => updateFormData('lastName', e.target.value)}
        />
      ),
    },
    {
      title: "Enter your date of birth",
      description: "We need this to verify your age.",
      component: (
        <Input
          placeholder="DD/MM/YYYY"
          type="date"
          onChange={(e) => updateFormData('dateOfBirth', e.target.value)}
        />
      ),
    },
    {
      title: "What's your gender?",
      description: "Please select your gender.",
      component: (
        <RadioGroup onChange={(value) => updateFormData('gender', value)}>
          <Stack spacing={4} direction="row">
            <Radio value="male">Male</Radio>
            <Radio value="female">Female</Radio>
            <Radio value="nonbinary">Non-Binary</Radio>
          </Stack>
        </RadioGroup>
      ),
    },
    {
      title: "What are you looking for?",
      description: "Select one option that best describes your intent.",
      component: (
        <RadioGroup onChange={(value) => updateFormData('lookingFor', value)}>
          <Stack spacing={4}>
            <Radio value="marriage">Get Married</Radio>
            <Radio value="relationship">Find a Relationship</Radio>
            <Radio value="friends">Chat and Meet Friends</Radio>
            <Radio value="culture">Know Other Cultures</Radio>
          </Stack>
        </RadioGroup>
      ),
      skippable: true,
    },
    // More steps can follow here based on the structure provided by the user.
  ];

  const updateFormData = (key, value) => {
    setFormData((prev) => ({ ...prev, [key]: value }));
  };

  const handleNext = () => {
    // Handle validation if necessary here
    if (step === 2 && formData.confirmationCode !== '123456') {
      toast({
        title: 'Invalid Code',
        description: 'Please check your email and enter the correct code.',
        status: 'error',
        duration: 5000,
        isClosable: true,
      });
      return;
    }
    setStep((prev) => prev + 1);
  };

  const handleSkip = () => {
    setStep((prev) => prev + 1);
  };

  const currentStep = steps[step - 1];

  return (
    <Box bg={bg} color={textColor} p={8} borderRadius="md" shadow="md">
      <Text fontSize="2xl" fontWeight="bold" bgGradient={instagramGradient} bgClip="text">
        {currentStep.title}
      </Text>
      <Text mt={2} mb={6} fontSize="md">
        {currentStep.description}
      </Text>
      <Box mb={6}>{currentStep.component}</Box>
      <Button
        colorScheme="pink"
        mr={4}
        onClick={handleNext}
        bgGradient={instagramGradient}
        _hover={{ bg: hoverColor }}
      >
        Next
      </Button>
      {currentStep.skippable && (
        <Button variant="ghost" onClick={handleSkip}>
          Skip
        </Button>
      )}
    </Box>
  );
};

export default MainRegister;
